<template>
  <div class="show_article" style="background-color: #fff">
    <div class="type_book">
      <el-tabs @tab-click="type_book" style="height: 40px" v-model="activeName">
        <el-tab-pane label="全部小说" name="all"></el-tab-pane>
        <el-tab-pane label="玄幻小说" name="玄幻小说"></el-tab-pane>
        <el-tab-pane label="仙侠小说" name="仙侠小说"></el-tab-pane>
        <el-tab-pane label="都市小说" name="都市小说"></el-tab-pane>
        <el-tab-pane label="历史军事" name="历史军事"></el-tab-pane>
        <el-tab-pane label="网游小说" name="网游小说"></el-tab-pane>
        <el-tab-pane label="科幻小说" name="科幻小说"></el-tab-pane>
        <el-tab-pane label="恐怖小说" name="恐怖小说"></el-tab-pane>
        <el-tab-pane label="其他小说" name="其他小说"></el-tab-pane>
      </el-tabs>
      <el-tabs @tab-click="is_end" style="height: 40px" v-model="activeName1">
        <el-tab-pane label="不限" name="all"></el-tab-pane>
        <el-tab-pane label="连载中" name="连载中"></el-tab-pane>
        <el-tab-pane label="已完成" name="已完成"></el-tab-pane>
      </el-tabs>
    </div>
    <div class="novel_info" ref="novel_info" v-for="item in book_list" :key="item"
         @click="load_book_see(item.book_url)">
      <img :src="item.book_image" alt="" class="book_img">
      <div class="book_info" :style="{width: novel_width}">
        <div class="book_top">
          <span class="span_info">{{ item.book_name }}</span>
          <span class="span_info">{{ item.book_author }}</span>
          <span class="span_info">类型:{{ item.book_type }}</span>
        </div>
        <div class="content_book">{{ item.book_brief }}</div>
        <div class="book_top">
          <span class="span_info">{{ item.book_is_end }}</span>
          <span class="span_info">{{ item.book_newest_time }}</span>
          <span class="span_info">{{ item.book_newest }}</span>
        </div>
      </div>
    </div>
  </div>
  <el-pagination
    :page-size="10"
    :pager-count="5"
    layout="prev, pager, next"
    :page-count="max_length"
    :hide-on-single-page="true"
    @current-change="current_change"
  >
  </el-pagination>
</template>

<script>
export default {
  name: 'book',
  data () {
    return {
      type: 'all',
      isEnd: 'all',
      page: 0,
      max_length: 0,
      book_list: [],
      activeName: 'all',
      activeName1: 'all',
      timer: '',
      novel_width: '720px'
    }
  },
  methods: {
    load_book_see (url) {
      this.$router.push({
        name: 'book_see',
        params: {
          url: url
        }
      })
    },
    type_book (tab, event) {
      this.type = tab.props.name
      this.initial()
    },
    is_end (tab, event) {
      this.isEnd = tab.props.name
      this.initial()
    },
    current_change (val) {
      this.page = val - 1
      this.initial()
    },
    initial () {
      this.api.initial.get_book(this.type, this.isEnd, this.page).then(res => {
        this.book_list = res.data.data
      })
    },
    novel_monitor () {
      const that = this
      that.timer = setInterval(function () {
        that.novel_width = (that.$refs.novel_info.clientWidth - 130) + 'px'
      }, 200)
    }
  },
  created () {
    this.initial()
    this.api.initial.get_book_len().then(res => {
      this.max_length = res.data.data
    })
    this.$nextTick(() => {
      this.novel_monitor()
    })
  },
  beforeUnmount () {
    clearInterval(this.timer)
  }
}
</script>

